/* tab css */

.tab-group {
  list-style: none;
  padding: 0;
  margin: 0 0 40px 0;
}
.tab-group:after {
  content: "";
  display: table;
  clear: both;
}
.tab-group li a {
  display: block;
  text-decoration: none;
  padding: 15px;
  background: rgba(160, 179, 176, 0.25);
  color: #a0b3b0;
  font-size: 20px;
  float: left;
  width: 50%;
  text-align: center;
  cursor: pointer;
  transition: 0.5s ease;
}
.tab-group li a:hover {
  background: #179b77;
  color: #ffffff;
}
.tab-group .active a {
  background: #1ab188;
  color: #ffffff;
}

.tab-content > div:last-child {
  display: none;
}

/* Home */
.container {
  display: block;
}

.intro {
  position: absolute;
  top: 55%;
  left: 50%;
  transform: translateX(-50%);
  font-size: 1.5rem;
  color: #333333;
}

.panel {
  position: absolute;
  left: 50%;
  bottom: 20%;
  transform: translateX(-50%);
  display: flex;
}

.pos-rel {
  margin-right: 20px;
}

figure.content-wrap {
  margin: 0;
  display: inline-block;
  margin-top: 15px;
}
.item figcaption {
  width: 150px;
  height: 120px;
  background-color: #cccc;
  border-radius: 10px;
  z-index: 0;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-box-shadow: 0 20px 70px rgba(136, 142, 160, 0.4);
  -moz-box-shadow: 0 20px 70px rgba(136, 142, 160, 0.4);
  box-shadow: 0 20px 70px rgba(136, 142, 160, 0.4);
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
}
.item-title {
  text-align: center;
  line-height: 120px;
  color: #5c6bc0;
  font-family: arial, sans-serif;
  font-size: 18px;
}

.item:hover .item-title {
  color: #fff;
}
.item:hover figcaption {
  background-color: #fcd001;
}
